import React from "react";
import { Link } from "react-router-dom";

function Home() {
  return (
    <div className="container mt-5 text-center">
      <div className="">
        <Link to="/registerStudent" className="btn btn-info">
          <h2 className="text-center">Student Registration</h2>
        </Link>
      </div>
      <div className="mt-5">
        <Link to="/registerFaculty" className="btn btn-info">
          <h2 className="text-center">Faculty Registration</h2>
        </Link>
      </div>
      <div className="mt-5">
        <Link to="/login" className="btn btn-info">
          <h2 className="text-center">Login form</h2>
        </Link>
      </div>
    </div>
  );
}

export default Home;
